import { button, div } from "motion/react-client";
import Link from "next/link";
import React from "react";
import { Spotlight } from "./ui/Spotlight";



const HeroSection = () => {
  return (
    <div className="h-auto md:h-[40rem] w-full rounded-md flex flex-col items-center justify-center relative overflow-hidden mx-auto py-10 md:py-0">
      <div className="p-4 relative z-10 w-full text-center">
        <Spotlight
          className="-top-30 left-0 md:-top-20 md:left-60"
          fill="white"
        />
        <h1 className="mt-20 md:mt-0 text-4xl md:text-7xl font-bold bg-clip-text text-transparent bg-gradient-to-b from-neutral-50 to-neutral-400">
          {" "}
          Master The Art Of Music
        </h1>
        <p
          className="mt-4 font-normal text-base md-text-lg 
                text-neutral-300 max-w-lg mx-auto"
        >
          Dive into our compensive music courses and transform your musical
          journey today. Whether you're a beginner or looking to refine your
          skills, join us to unlock your true potential.
        </p>
        <div className="mt-5">
          <Link href={"/courses"}>
           <button type='button' className='py-4.5 z-19 px-6 text-sm rounded-full border border-solid border-gray-200 text-gray-600 cursor-pointer font-semibold text-center shadow-xs transition-all duration-500 hover:bg-gray-300 hover:text-gray-800'>
           Explore courses
            </button>
      </Link> 
        </div>
      </div>
    </div>
  );
};

export default HeroSection;
